.popup {
    display: flex;
    visibility: hidden;
    justify-content: center;
    align-items: center;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 10;
    opacity: 0;
    transition: visibility 0.5s, opacity 0.5s linear;
    background-color: rgba(0, 0, 0, 0.5);
}

.popup_opened {
    visibility: visible;
    opacity: 1;
}

.popup__btn-close:hover {
    opacity: 0.6;
}

@media screen and (max-width: 767px) {
    .popup__btn-close {
        width: 20px;
        height: 20px;
        top: -36px;
        right: 0;
    }
}

.popup__btn-close {
    display: block;
    padding: 0px;
    right: 20px;
    top: 20px;
    width: 20px;
    height: 20px;
    border: none;
    background-position: center;
    background-repeat: no-repeat;
    background-size: auto;
    background-image: url(../../img/close-black.svg);
    position: absolute;
    background-color: #FCFCFC;
}

.popup__container {
    display: flex;   
    width: 568px;
    margin: 20px auto;
    border: 2px solid #F8D24E;
    background-color: #FCFCFC;
    color: #131313;
    font-family: 'Montserrat';
    font-style: normal;
    border-radius: 10px;
    box-sizing: border-box;
    position: relative;    
    z-index: 100;
    padding: 30px;
}

.popup__center {
    display: flex;    
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    padding: 0;
    z-index: 50;
}

.popup__title {
    width: 273px;
    font-family: 'Seymour One';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.4;
    text-align: center;
    margin: 0 auto;
}

.popup__form {
    max-width: 460px;
    box-sizing: border-box;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    padding: 0;
}

.popup__choices {
    width: 460px;
    height: 132px;
    border: none;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 31px;
    margin: 106px auto 40px;
    padding: 0;
}

.popup__choice_ice {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 132px;
    height: 135px;
    display: flex;
    flex-direction: column;
    padding: 0;
    cursor: pointer;
    transition: 0.2s all linear;
    outline: none;
    overflow: hidden;
    position: absolute;
    top: 163px;
    left: 54px;
    border-radius: 16px;
    border: 2px solid #96AF8C;
    background-color: #FCFCFC;
    box-shadow: 0px 5.26027px 19.726px rgba(212, 20, 67, 0.1);
}

.popup__choice_coffee {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 132px;
    height: 135px;
    display: flex;
    flex-direction: column;
    padding: 0;
    cursor: pointer;
    transition: 0.2s all linear;
    outline: none;
    overflow: hidden;
    position: absolute;
    top: 164px;
    left: 218px;
    border-radius: 16px;
    border: 2px solid #EFD478;
    box-shadow: 0px 5.26027px 19.726px rgba(212, 20, 67, 0.1);
    background-color: #FCFCFC;
}

.popup__choice_shake {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 132px;
    height: 135px;
    display: flex;
    flex-direction: column;
    padding: 0;
    cursor: pointer;
    transition: 0.2s all linear;
    outline: none;
    overflow: hidden;
    position: absolute;
    top: 163px;
    right: 51px;
    border-radius: 16px;
    border: 2px solid #D6936D;
    background-color: #FCFCFC;
    filter: drop-shadow(0px 5.26027px 19.726px rgba(212, 20, 67, 0.1));
}

.popup__choice_ice:checked {
    background: #96AF8C;
    border: none;
}

.popup__choice_coffee:checked {
    background: #EFD478;
    border: none;
}

.popup__choice_shake:checked {
    background: #D6936D;
    border: none;
}

.popup__ice {
    position: absolute;
    top: 163px;
    left: 54px;
    width: 132px;
    height: 135px;
    border-radius: 16px;
}

.popup__coffee {
    position: absolute;
    top: 164px;
    left: 218px;
    width: 132px;
    height: 135px;
    border-radius: 16px;
}

.popup__shake {
    position: absolute;
    top: 163px;
    right: 51px;
    width: 132px;
    height: 135px;
    border-radius: 16px;
}

.popup__image {
    box-sizing: border-box;
    display: block;
    border-radius: 70px;
    border: none;
    margin: 0;
    padding: 0;
    object-fit: contain;
    width: 132px;
    height: 132px;
    position: absolute;
    top: -66px;
    padding: 0;
    z-index: 50;
}

.popup__choice-circle {
    display: block;
    border-radius: 70px;
    border: none;
    width: 132px;
    height: 132px;
    object-fit: fill;
    top: -58px;
    position: absolute;
}

.popup__circle_ice {
    background: #AEC6A5;
    background-image: url(../../img/Ellipse-10.png);
    box-shadow: 0px 5.26027px 19.726px rgba(10, 136, 23, 0.25);
}

.popup__circle_coffee {
    background-color: #EFD478;
    background-image: url(../../img/Ellipse-11.svg);
    box-shadow: 0px 5.26027px 19.726px rgba(153, 114, 23, 0.25);
}

.popup__circle_shake {
    background: #D6936D;
    background-image: url(../../img/Ellipse-12.svg);
    box-shadow: 0px 5.26027px 19.726px rgba(0, 0, 0, 0.25);
}

.popup__description {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    padding: 0;    
    bottom: 5px;
    left: 15px;
    position: absolute;
}

.buy_circle {
    box-sizing: border-box;
    width: 53px;
    height: 53px;
    background: #9CBE8F;
    border-radius: 40px;
    box-shadow: 0px 6px 20px rgba(10, 136, 23, 0.25);
    font-weight: 500;
    font-size: 12px;
    line-height: 28px;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.popup__form-set {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    border: none;
    padding: 0;
}

.popup__field {
    height: 40.4px;
    display: flex;
    align-items: center;
    background: rgba(248, 210, 78, 0.1);
    border: 1px solid #84A178;
    border-radius: 10px;
    padding: 0px 15px;
    margin-bottom: 16px;
}

.popup__field_comment {
    height: 57px;
    margin-bottom: 32px;
    align-items: start;
    padding: 11px 16px;
}

.popup__input {
    border: none;
    outline: none;
    background: inherit;
    color: inherit;
    padding: 0;
    margin: 0;
    width: 100%;
}

.popup__button {
    box-sizing: border-box;
    border: none;
    display: flex;
    width: 121px;
    height: 42px;
    background: #F8D24E;
    border-radius: 10px;
    padding: 12px 34px;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    align-items: center;
    text-align: center;
    justify-content: center;
    margin: 0 auto;
    cursor: pointer;
}

.popup__button:disabled {
    background: #F8D24E; 
    border: 2px solid #14cf65;
    color: #14cf65; 
}

@media screen and (max-width: 600px) {
    .popup__container {
        width: 280px;
        height: 100vh;
        padding: 0;
    }

    .popup__btn-close {
        right: 8px;
        top: 8px;
        width: 16px;
        height: 16px;
        z-index: 10;
    }

    .popup__center {
        width: 224px;
        padding: 0;
        margin: 0 auto;
    }

   .popup__title {
    font-size: 16px;
    padding: 10px 0; 
    position: absolute;
    left: 0px;
   } 

    .popup__form {
        max-width: 224px;
        box-sizing: border-box;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        padding: 0;
    }

    .popup__choices {
        width: 132px;
        height: 540px;
        grid-template-rows: repeat(3, 1fr);
        gap: 10px;        
        margin: 0 auto;            
    }

    .popup__choice_ice {       
        height: 100px;
        top: 110px;
        left: 74px; 
    }

    .popup__choice_coffee {          
        height: 100px;
        top: 280px;
        left: 74px;
    }

    .popup__choice_shake {              
        height: 100px;
        top: 450px;
        left: 74px;
    }   

    .popup__ice {
        top: 110px;      
        height: 100px;
        left: 74px; 
    }

    .popup__coffee {
        top: 280px;       
        height: 100px;
        left: 74px;
    }

    .popup__shake {
        top: 450px;        
        height: 100px;
        left: 74px;
    }

    .popup__description {
        margin: 0 0 10px;
        font-size: 14px;
        padding: 0 10px;
    }

   .popup__form-set {
      margin: 20px auto 10px;
    }

    .popup__field_comment {
        margin-bottom: 10px;
        height: 30px;
        font-size: 12px;
        padding: 5px;
    } 

    .popup__input {
        padding: 8px;
        font-size: 12px;
    }
    
    .popup__field {
        height: 30px;        
        padding: 0px 10px;
        margin-bottom: 10px;
    }   
   
    .popup__button {
        width: 132px;        
        height: 32px;        
        padding: 9px 34px;        
        font-size: 12px;
        line-height: 15px;                
    }    
}